import React, { useEffect, useState } from "react";
import { fetchHelloMessage, hello } from "../api/mainApi";

const HelloWorld = () => {
    const [message, setMessage] = useState("");

    useEffect(() => {
        fetchHelloMessage()
            .then((response) => {
                setMessage(response.data); // 서버로부터 받은 데이터를 상태에 저장
            })
            .catch((error) => {
                console.error("Error fetching the hello message:", error);
                setMessage("Failed to fetch message"); // 에러 처리
            });
    }, []);

    return <div>{message}</div>; // 상태에 저장된 메시지를 화면에 표시
};

export default HelloWorld;
